import * as React from 'react';
import 'App.css';
import LoadingButton from '@mui/lab/LoadingButton';
import _ from 'lodash';
import WalletModel from 'wallet';
import { hasString, getValue } from 'utils/helper';
import { formatNumber4Loans, formatPercentages4Loans } from 'utils/format';
import { bigGreaterThanOrEqualTo, bigGreaterThan, isNumeric, mul } from 'utils/operation';
import { MAX_UINT256, useIntervalRefresh } from 'wallet/tools';
import { formatNumberInputValue } from 'utils/input';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { STAKING } from 'wallet/wallet';

// wallet business
export const TARGET_CHAINID = [42161];

const StakingView = props => {
  const stakeStyle = {
    flexDirection: 'column',
    flex: '1',
    padding: '20px',
    justifyContent: 'space-between',
    margin: '10px',
  };

  const detailStyle = {
    flexDirection: 'column',
    marginBottom: '20px',
  };

  const detailText = {
    color: '#555555',
  };

  const inputStyleEnabled = {
    outline: 'none',
    lineHeight: '20px',
    paddingLeft: '8px',
    borderRadius: '4px',
    width: '60%',
    border: '1px solid #668cf6',
  };
  const inputStyleDisabled = {
    outline: 'none',
    lineHeight: '20px',
    paddingLeft: '8px',
    borderRadius: '4px',
    width: '60%',
    border: '1px solid gray',
  };
  const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 300,
    bgcolor: 'background.paper',
    border: '2px solid #668cf6',
    boxShadow: 24,
    p: 4,
  };

  const { data } = props ?? {};

  const {
    balanceOf,
    stakeShare,
    withdrawShare,
    address,
    network,
    approve,
    allowance: getAllowance,
    getStakingBalance,
    getNav,
    getSharedTotalStake,
    getSharedStakingAPR,
  } = WalletModel.useContext();

  const [allowance, setAllowance] = React.useState();

  const [locked, setLocked] = React.useState();

  const [balance, setBalance] = React.useState();

  const [nav, setNav] = React.useState({ m: 0, a: 0, b: 0 });

  const [shareTotalStake, setShareTotalStake] = React.useState();

  const [stakeRate, setStakeRate] = React.useState();

  const refresh = React.useCallback(async () => {
    try {
      const b = await balanceOf(data?.contract);
      setBalance(b);
      const a = await getAllowance(data?.contract, STAKING);
      setAllowance(a);
      const sb = await getStakingBalance(data?.trancheId);
      setLocked(sb);
      const gnav = await getNav();
      setNav(gnav);
      const gsts = await getSharedTotalStake(data?.contract);
      setShareTotalStake(gsts);
      const _stakeRate = await getSharedStakingAPR(data?.contract, data?.trancheId);
      setStakeRate(_stakeRate);
    } catch (err) {
      //
    }
  }, [balanceOf, data, getAllowance, getStakingBalance, getNav, getSharedTotalStake, getSharedStakingAPR]);

  useIntervalRefresh(refresh);

  const [loadingApprove, setLoadingApprove] = React.useState();
  const [loadingStake, setLoadingStake] = React.useState();
  const [loadingWithdraw, setLoadingWithdraw] = React.useState();

  const verify = React.useCallback(() => {
    if (!hasString(address)) {
      setModalData({ open: true, message: 'connect wallet first' });
      return false;
    }
    if (!_.includes(TARGET_CHAINID, network?.chainId)) {
      setModalData({ open: true, message: 'wrong network' });
      return false;
    }
    return true;
  }, [network, address]);

  const [stakeAmount, setStakeAmount] = React.useState('');
  const handleStakeInput = React.useCallback(event => {
    const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
    const value = formatNumberInputValue(newValue, 18);
    setStakeAmount(value);
  }, []);

  const handleStakeAll = React.useCallback(() => {
    setStakeAmount(balance);
  }, [balance]);

  const [withdrawAmount, setWithdrawAmount] = React.useState('');
  const handleWithdrawInput = React.useCallback(event => {
    const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
    const value = formatNumberInputValue(newValue, 18);
    setWithdrawAmount(value);
  }, []);

  const handleWithdrawAll = React.useCallback(() => {
    setWithdrawAmount(locked);
  }, [locked]);

  const handleApprove = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingApprove(true);
      const tx = await approve(data?.contract, STAKING, MAX_UINT256);
      await tx?.wait();
      const a = await getAllowance(data?.contract, STAKING);
      setAllowance(a);
      setLoadingApprove(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingApprove(false);
    }
  }, [verify, data, approve, getAllowance]);

  const handleStake = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingStake(true);
      if (bigGreaterThan(stakeAmount, balance)) {
        setModalData({ open: true, message: 'Balance not enough' });
        return;
      }
      const tx = await stakeShare(data?.trancheId, stakeAmount);
      await tx?.wait();
      const sb = await getStakingBalance(data?.trancheId);
      setLocked(sb);
      setStakeAmount('');
      setLoadingStake(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingStake(false);
    }
  }, [verify, balance, stakeShare, data, stakeAmount, getStakingBalance]);

  const handleWithdraw = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingWithdraw(true);
      if (bigGreaterThan(withdrawAmount, locked)) {
        setModalData({ open: true, message: 'Withdraw exceeding your stake balance' });
        return;
      }
      const tx = await withdrawShare(data?.trancheId, withdrawAmount);
      await tx?.wait();
      const sb = await getStakingBalance(data?.trancheId);
      setLocked(sb);
      setWithdrawAmount('');
      setLoadingWithdraw(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingWithdraw(false);
    }
  }, [verify, locked, withdrawShare, data, withdrawAmount, getStakingBalance]);

  // const stakeSymbol = hasString(stakeToken?.symbol) ? stakeToken?.symbol : BLANK_CHAR;

  let approveVerify = false;
  if (hasString(stakeAmount)) {
    approveVerify = bigGreaterThanOrEqualTo(allowance, stakeAmount);
  } else {
    approveVerify = bigGreaterThan(allowance, 0);
  }
  if (data?.noneedApprove) {
    approveVerify = true;
  }

  // modal func
  const handleClose = () => setModalData({ open: false });
  const [modalData, setModalData] = React.useState({
    open: false,
    message: '',
  });

  // let extra;
  // if (hasPlainObject(data?.extra?.warning)) {
  //   extra = <Warning {...data?.extra?.warning} />;
  // }
  // if (hasPlainObject(data?.extra?.link)) {
  //   extra = <Link {...data?.extra?.link} />;
  // }

  return (
    <div
      className="stakingDetail"
      style={{
        flexDirection: 'row wrap',
        justifyContent: 'space-between',
        width: '100%',
      }}
    >
      <div className="forms-bg" style={stakeStyle}>
        <div style={detailStyle}>
          <h2>{data?.name}</h2>
          <div>
            <h5>
              NAV.
              {`${formatNumber4Loans(data?.trancheId === 0 ? nav.m : data?.trancheId === 1 ? nav.a : nav.b, {
                places: 2,
              })}`}
            </h5>
          </div>
          <div style={{ justifyContent: 'space-between' }}>
            <h5>Total staked</h5>
            <h5>{`${formatNumber4Loans(shareTotalStake, { places: 2 })} ${data?.name}`}</h5>
          </div>
        </div>
        <div style={detailStyle}>
          <h3 style={detailText}>Staking APR</h3>
          <h2>{formatPercentages4Loans(mul(stakeRate, 365))}</h2>
        </div>

        <div
          style={{
            justifyContent: 'space-between',
          }}
        >
          <h5 style={detailText}>You staked</h5>
          <h5>{`${formatNumber4Loans(locked, { places: 2 })} ${data?.name}`}</h5>
          <span style={{ cursor: 'pointer' }} onClick={handleWithdrawAll}>
            <h5>MAX</h5>
          </span>
        </div>
        <div style={detailStyle}>
          <div style={{ justifyContent: 'space-between', marginTop: '8px' }}>
            <input
              type="text"
              style={!data?.inputDisabled1 ? inputStyleEnabled : inputStyleDisabled}
              disabled={data?.inputDisabled1}
              onChange={handleWithdrawInput}
              value={withdrawAmount}
            />
            <LoadingButton
              variant="outlined"
              loading={loadingWithdraw}
              disabled={
                data?.unStakeBtn?.disabled
                  ? true
                  : !isNumeric(withdrawAmount) || bigGreaterThanOrEqualTo(0, withdrawAmount)
              }
              size="small"
              onClick={handleWithdraw}
            >
              Unstake
            </LoadingButton>
          </div>
        </div>

        <div
          style={{
            justifyContent: 'space-between',
          }}
        >
          <h5 style={detailText}>Balance</h5>
          <h5>{`${formatNumber4Loans(balance, { places: 2 })} ${data?.name}`}</h5>
          <span style={{ cursor: 'pointer' }} onClick={handleStakeAll}>
            <h5>MAX</h5>
          </span>
        </div>
        <div style={detailStyle}>
          <div style={{ justifyContent: 'space-between', marginTop: '8px' }}>
            <input
              type="text"
              style={!data?.inputDisabled2 ? inputStyleEnabled : inputStyleDisabled}
              disabled={data?.inputDisabled2}
              onChange={handleStakeInput}
              value={stakeAmount}
            />
            {approveVerify ? (
              <LoadingButton
                variant="contained"
                loading={loadingStake}
                size="small"
                onClick={handleStake}
                disabled={
                  data?.stakeBtn?.disabled ? true : !isNumeric(stakeAmount) || bigGreaterThanOrEqualTo(0, stakeAmount)
                }
              >
                Stake
              </LoadingButton>
            ) : (
              <LoadingButton
                variant="contained"
                loading={loadingApprove}
                disabled={data?.stakeBtn?.disabled}
                size="small"
                onClick={handleApprove}
              >
                Approve
              </LoadingButton>
            )}
          </div>
        </div>
      </div>

      <div>
        <Modal
          open={modalData?.open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-description">{modalData?.message}</Typography>
          </Box>
        </Modal>
      </div>
    </div>
  );
};

export default StakingView;
