import * as React from 'react';
import 'App.css';
import LoadingButton from '@mui/lab/LoadingButton';
import _ from 'lodash';
import WalletModel from 'wallet';
import { hasString, getValue } from 'utils/helper';
import { formatNumber4Loans, formatPercentages4Loans } from 'utils/format';
import { bigGreaterThanOrEqualTo, bigGreaterThan, BLANK_CHAR, isNumeric, safeSub, mul } from 'utils/operation';
import { MAX_UINT256, useIntervalRefresh } from 'wallet/tools';
import { formatNumberInputValue } from 'utils/input';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';

const PoolViews = props => {
  const stakeStyle = {
    flexDirection: 'column',
    flex: '1',
    padding: '20px',
    justifyContent: 'space-between',
    margin: '10px',
  };

  const detailStyle = {
    flexDirection: 'column',
    marginBottom: '20px',
  };

  const detailText = {
    color: '#555555',
  };

  const inputStyleEnabled = {
    outline: 'none',
    lineHeight: '20px',
    paddingLeft: '8px',
    borderRadius: '4px',
    width: '60%',
    border: '1px solid #668cf6',
  };
  const inputStyleDisabled = {
    outline: 'none',
    lineHeight: '20px',
    paddingLeft: '8px',
    borderRadius: '4px',
    width: '60%',
    border: '1px solid gray',
  };
  const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 300,
    bgcolor: 'background.paper',
    border: '2px solid #668cf6',
    boxShadow: 24,
    p: 4,
  };

  // wallet business
  const TARGET_CHAINID = [42161];
  const { data } = props ?? {};

  const { address, network, approve, stakeTokenInfo, stake, withdraw, earned, getLPReward, totalClaimed, apr } =
    WalletModel.useContext();

  const verify = React.useCallback(() => {
    if (!hasString(address)) {
      setModalData({ open: true, message: 'connect wallet first' });
      return false;
    }
    if (!_.includes(TARGET_CHAINID, network?.chainId)) {
      setModalData({ open: true, message: 'wrong network' });
      return false;
    }
    return true;
  }, [network, address]);

  const [stakeToken, setStakeToken] = React.useState();
  const [earn, setEarn] = React.useState();
  const [aprDay, setAprDay] = React.useState();

  const [loadingApprove, setLoadingApprove] = React.useState();
  const [loadingStake, setLoadingStake] = React.useState();
  const [loadingUnstake, setLoadingUnstake] = React.useState();
  const [loadingClaim, setLoadingClaim] = React.useState();

  const refresh = React.useCallback(async () => {
    try {
      const _stakeToken = await stakeTokenInfo(data?.contract);
      setStakeToken(_stakeToken);
      const _earn = await earned(data?.contract);
      setEarn(_earn);
      const _apr = await apr(data?.contract, data?.name);
      setAprDay(_apr);
    } catch (err) {
      // console.log(err);
    }
  }, [stakeTokenInfo, earned, data, totalClaimed, apr]);

  useIntervalRefresh(refresh);

  const [stakeAmount, setStakeAmount] = React.useState('');
  const handleStakeInput = React.useCallback(
    event => {
      const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
      const value = formatNumberInputValue(newValue, stakeToken?.decimals);
      setStakeAmount(value);
    },
    [stakeToken]
  );

  const handleStakeAll = React.useCallback(() => {
    setStakeAmount(stakeToken?.walletBalance);
  }, [stakeToken]);

  const [withdrawAmount, setWithdrawAmount] = React.useState('');
  const handleWithdrawInput = React.useCallback(
    event => {
      const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
      const value = formatNumberInputValue(newValue, stakeToken?.decimals);
      setWithdrawAmount(value);
    },
    [stakeToken]
  );

  const handleWithdrawAll = React.useCallback(() => {
    setWithdrawAmount(stakeToken?.stakeBalance);
  }, [stakeToken]);

  const handleApprove = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingApprove(true);
      const tx = await approve(stakeToken?.address, data?.contract, MAX_UINT256);
      await tx?.wait();
      const _stakeToken = await stakeTokenInfo(data?.contract);
      setStakeToken(_stakeToken);
      setLoadingApprove(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingApprove(false);
    }
  }, [verify, approve, stakeToken, data, stakeTokenInfo]);

  const handleStake = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingStake(true);
      if (bigGreaterThan(stakeAmount, stakeToken?.walletBalance)) {
        setModalData({ open: true, message: 'Balance not enough' });
        return;
      }
      const tx = await stake(data?.contract, stakeAmount);
      await tx?.wait();
      setStakeAmount('');
      const _stakeToken = await stakeTokenInfo(data?.contract);
      setStakeToken(_stakeToken);
      setLoadingStake(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingStake(false);
    }
  }, [verify, stakeToken, stake, data, stakeAmount, stakeTokenInfo]);

  const handleWithdraw = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingUnstake(true);
      if (bigGreaterThan(withdrawAmount, stakeToken?.stakeBalance)) {
        setModalData({ open: true, message: 'Withdraw exceeding your stake balance' });
        return;
      }
      const tx = await withdraw(data?.contract, withdrawAmount);
      await tx?.wait();
      setWithdrawAmount('');
      const _stakeToken = await stakeTokenInfo(data?.contract);
      setStakeToken(_stakeToken);
      setLoadingUnstake(false);
    } catch (err) {
      setLoadingUnstake(false);
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
    }
  }, [verify, stakeToken, withdraw, data, withdrawAmount, stakeTokenInfo]);

  const handleClaim = React.useCallback(async () => {
    if (!verify()) {
      return;
    }
    try {
      setLoadingClaim(true);
      const tx = await getLPReward(data?.contract);
      await tx?.wait();
      const _stakeToken = await stakeTokenInfo(data?.contract);
      setStakeToken(_stakeToken);
      setLoadingClaim(false);
    } catch (err) {
      setModalData({ open: true, message: err?.data?.message ? err?.data?.message : 'Execution reverted' });
      setLoadingClaim(false);
    }
  }, [verify, getLPReward, data, stakeTokenInfo]);

  const stakeSymbol = hasString(stakeToken?.symbol) ? stakeToken?.symbol : BLANK_CHAR;

  let approveVerify = false;
  if (hasString(stakeAmount)) {
    approveVerify = bigGreaterThanOrEqualTo(stakeToken?.allowance, stakeAmount);
  } else {
    approveVerify = bigGreaterThan(stakeToken?.allowance, 0);
  }
  if (data?.noneedApprove) {
    approveVerify = true;
  }

  // modal func
  const handleClose = () => setModalData({ open: false });
  const [modalData, setModalData] = React.useState({
    open: false,
    message: '',
  });

  // let extra;
  // if (hasPlainObject(data?.extra?.warning)) {
  //   extra = <Warning {...data?.extra?.warning} />;
  // }
  // if (hasPlainObject(data?.extra?.link)) {
  //   extra = <Link {...data?.extra?.link} />;
  // }

  return (
    <div
      className="stakingDetail"
      style={{
        flexDirection: 'row wrap',
        justifyContent: 'space-between',
        width: '100%',
      }}
    >
      <div className="forms-bg" style={stakeStyle}>
        <div style={detailStyle}>
          <h2>{data?.period}</h2>
          <div style={{ justifyContent: 'space-between' }}>
            <h5>End time</h5>
            <h5>{data?.endTime}</h5>
          </div>
          <div style={{ justifyContent: 'space-between' }}>
            <h5>Total staked</h5>
            <h5>{`${formatNumber4Loans(stakeToken?.totalStaked)} ${stakeSymbol}`}</h5>
          </div>
        </div>
        <div style={detailStyle}>
          <h3 style={detailText}>Staking APR</h3>
          <h2>{formatPercentages4Loans(mul(aprDay?.apr, 365))}</h2>
        </div>

        <div style={detailStyle}>
          <h3 style={detailText}>You rewards</h3>
          <div
            style={{
              flexDirection: 'row',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
          >
            <h5>{`${formatNumber4Loans(earn)} ACHESS`}</h5>
            <LoadingButton
              loading={loadingClaim}
              variant="outlined"
              disabled={data?.claimBtn?.disabled}
              size={'small'}
              onClick={handleClaim}
            >
              Claim
            </LoadingButton>
          </div>
        </div>

        <div
          style={{
            justifyContent: 'space-between',
          }}
        >
          <h5 style={detailText}>Staked</h5>
          <h5>{`${formatNumber4Loans(stakeToken?.stakeBalance)} ${stakeSymbol}`}</h5>
          <span style={{ cursor: 'pointer' }} onClick={handleWithdrawAll}>
            <h5>MAX</h5>
          </span>
        </div>
        <div style={detailStyle}>
          <div style={{ justifyContent: 'space-between', marginTop: '8px' }}>
            <input
              type="text"
              style={data?.inputDisabled1 ? inputStyleDisabled : inputStyleEnabled}
              disabled={data?.inputDisabled1 ? true : !stakeToken?.stakeBalance}
              onChange={handleWithdrawInput}
              value={withdrawAmount}
            />
            <LoadingButton
              variant="outlined"
              loading={loadingUnstake}
              disabled={
                data?.unStakeBtn?.disabled
                  ? true
                  : !isNumeric(withdrawAmount) || bigGreaterThanOrEqualTo(0, withdrawAmount)
              }
              size="small"
              onClick={handleWithdraw}
            >
              Unstake
            </LoadingButton>
          </div>
        </div>

        <div
          style={{
            justifyContent: 'space-between',
          }}
        >
          <h5 style={detailText}>Balance</h5>
          <h5>{`${formatNumber4Loans(stakeToken?.walletBalance)} ${stakeSymbol}`}</h5>
          <span style={{ cursor: 'pointer' }} onClick={handleStakeAll}>
            <h5>MAX</h5>
          </span>
        </div>
        <div style={detailStyle}>
          <div style={{ justifyContent: 'space-between', marginTop: '8px' }}>
            <input
              type="text"
              style={!data?.inputDisabled1 ? inputStyleEnabled : inputStyleDisabled}
              disabled={data?.inputDisabled2}
              onChange={handleStakeInput}
              value={stakeAmount}
            />
            {approveVerify ? (
              <LoadingButton
                loading={loadingStake}
                variant="contained"
                size="small"
                onClick={handleStake}
                disabled={
                  data?.stakeBtn?.disabled ? true : !isNumeric(stakeAmount) || bigGreaterThanOrEqualTo(0, stakeAmount)
                }
              >
                Stake
              </LoadingButton>
            ) : (
              <LoadingButton
                loading={loadingApprove}
                variant="contained"
                disabled={data?.stakeBtn?.disabled}
                size="small"
                onClick={handleApprove}
              >
                Approve
              </LoadingButton>
            )}
          </div>
        </div>
        <a
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            margin: '15px 0',
            textDecoration: 'none',
            fontSize: '0.8rem',
          }}
          className="A-link"
          href={data?.extra?.link?.link}
          target="_blank"
          rel="noreferrer"
        >
          {data?.extra?.link?.text}
        </a>
      </div>

      <div>
        <Modal
          open={modalData?.open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-description">{modalData?.message}</Typography>
          </Box>
        </Modal>
      </div>
    </div>
  );
};

export default PoolViews;
