import * as React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import FormsBuy from './FormsBuy';
import FormsSell from './FormsSell';
import { STAKETOKENS } from 'wallet/stakeTokens';
import Checkbox from '@mui/material/Checkbox';

function TabPanel(props) {
  const { children, value, index } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
    >
      {value === index && (
        <div
          style={{
            flexFlow: 'row wrap',
            width: '100%',
            justifyContent: 'center',
          }}
        >
          {children}
        </div>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    'id': `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function BasicTabs(props) {
  const { token } = props ?? {};
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const data = _.find(STAKETOKENS, item => item.name === token);

  const [checked, setChecked] = React.useState(false);

  const handleChecked = React.useCallback(event => {
    setChecked(event?.target?.checked ?? false);
  }, []);

  return (
    <Box
      sx={{
        width: '100%',
        flexDirection: 'column',
        mt: '10px',
      }}
    >
      <Box
        sx={{
          position: 'relative',
          borderBottom: 1,
          borderColor: 'divider',
        }}
      >
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="Buy" {...a11yProps(0)} />
          <Tab label="Sell" {...a11yProps(1)} />
        </Tabs>
        <div
          style={{
            position: 'absolute',
            right: '20px',
            top: '50%',
            display: 'flex',
            flexFlow: 'row nowrap',
            alignItems: 'center',
            transform: 'translateY(-50%)',
          }}
        >
          <Checkbox checked={checked} onChange={handleChecked} />
          Post-only
        </div>
      </Box>
      <TabPanel value={value} index={0}>
        <FormsBuy data={data} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <FormsSell data={data} />
      </TabPanel>
    </Box>
  );
}
