import * as React from 'react';
import _ from 'lodash';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import LoadingButton from '@mui/lab/LoadingButton';
import WalletModel from 'wallet';
import { ETH_TOKEN_ADDRESS, useIntervalRefresh } from 'wallet/tools';
import { formatNumber4Loans } from 'utils/format';
import { getValue, hasString, hasValue } from 'utils/helper';
import { formatNumberInputValue } from 'utils/input';
import { bigGreaterThan, bigLessThan, mul, BLANK_CHAR } from 'utils/operation';
import { Divider } from '@mui/material';
import { Stepper } from 'components/Stepper';

export default function InputAdornments(props) {
  const { data } = props ?? {};
  const { balanceOf, getQueenPrice, create } = WalletModel.useContext();

  const min = 0.01;

  // const [disabled, setDisabled] = React.useState(true);
  const [balance, setBalance] = React.useState();
  const [amount, setAmount] = React.useState();
  const [queenPrice, setQueenPrice] = React.useState();

  const [values, setValues] = React.useState({
    amount: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });

  const handleChange = React.useCallback(
    prop => event => {
      const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
      const value = formatNumberInputValue(newValue, 18);
      setValues({ weight: value });
      setAmount(value);
    },
    []
  );

  const refresh = React.useCallback(async () => {
    try {
      const b = await balanceOf(ETH_TOKEN_ADDRESS);
      setBalance(b);
      const qp = await getQueenPrice();
      setQueenPrice(qp);
    } catch (err) {
      //
    }
  }, [balanceOf, getQueenPrice]);

  useIntervalRefresh(refresh);

  const [loading, setLoading] = React.useState();
  const handleClick = React.useCallback(async () => {
    try {
      setLoading(true);
      const tx = await create(amount);
      await tx?.wait();
      setValues({ weight: '' });
      setAmount('');
    } catch (err) {}
    setLoading(false);
  }, [create, amount]);

  const disabled =
    !hasString(amount) || !hasValue(balance) || bigLessThan(amount, min) || bigGreaterThan(amount, balance);

  const handleCreatefromAll = React.useCallback(() => {
    setValues({ weight: balance });
    setAmount(balance);
  }, [balance]);

  const queenAmount = mul(amount, queenPrice);

  return (
    <Box
      sx={{
        display: 'flex',
        flexWrap: 'wrap',
        paddingTop: '20px',
      }}
    >
      <div>
        <FormControl sx={{ m: 1, width: '25ch' }} variant="outlined">
          <OutlinedInput
            id="outlined-adornment-weight"
            value={values.weight}
            onChange={handleChange('weight')}
            placeholder={`Min ${min}`}
            endAdornment={<InputAdornment position="end">{`${data?.name}`}</InputAdornment>}
            aria-describedby="outlined-weight-helper-text"
            inputProps={{
              'aria-label': 'weight',
            }}
          />
          <div style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
            <h5>{`Available: ${formatNumber4Loans(balance, { places: 4 })} ${data?.name}`}</h5>
            <span style={{ cursor: 'pointer' }} onClick={handleCreatefromAll}>
              <h5>MAX</h5>
            </span>
          </div>
          <div style={{ justifyContent: 'center', padding: '20px 0' }}>
            <ArrowDownwardIcon />
          </div>
          <h3>{`Est. ${formatNumber4Loans(queenAmount, { places: 2 }) ?? BLANK_CHAR} USDC`}</h3>
          <h5>{`Based on last NAV ${formatNumber4Loans(queenAmount, { places: 2 }) ?? BLANK_CHAR}`}</h5>
          <Divider />
          <div style={{ justifyContent: 'space-between', alignItems: 'center', marginTop: '8px' }}>
            <div style={{ fontSize: '0.6rem' }}>Premium/Discount</div>
            <Stepper defaultValue="-2" />
          </div>
          <LoadingButton
            variant="contained"
            size="medium"
            style={{ margin: '20px 0' }}
            disabled={disabled}
            loading={loading}
            onClick={handleClick}
          >
            Place order
          </LoadingButton>
          <div>
            <h5 style={{ textAlign: 'center' }}>{`Trades are expected to be settled instant`}</h5>
          </div>
        </FormControl>
      </div>
    </Box>
  );
}
