import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import LoadingButton from '@mui/lab/LoadingButton';
import { mul, div, bigLessThanOrEqualTo } from 'utils/operation';
import { endOfDayPlus15Plus1, formatNumber4Loans } from 'utils/format';
import WalletModel from 'wallet';
import { QUEEN } from 'wallet/wallet';
import { useIntervalRefresh } from 'wallet/tools';
import { hasString } from 'utils/helper';
import { useHistory } from 'react-router-dom';
import { STAKE } from 'router/CONSTANTS';

function TabPanel(props) {
  const { children, value, index } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
    >
      {value === index && (
        <div
          style={{
            flexFlow: 'row wrap',
            width: '100%',
            justifyContent: 'center',
          }}
        >
          {children}
        </div>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    'id': `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const Claimable = React.memo(props => {
  const {
    balanceOf,
    getQueenPrice,
    claimQuee,
    // claimAndStakeQuee,
    getCreationRedemption,
    currentDay,
    doSettle,
  } = WalletModel.useContext();

  const [balance, setBalance] = React.useState();
  const [queenPrice, setQueenPrice] = React.useState();

  const [creationRedemption, setCreationRedemption] = React.useState();
  const [currentDayTimestamp, setCurrentDay] = React.useState();

  const refresh = React.useCallback(async () => {
    try {
      const b = await balanceOf(QUEEN);
      setBalance(b);
      const qp = await getQueenPrice();
      setQueenPrice(qp);
      const gc = await getCreationRedemption();
      setCreationRedemption(gc);
      const _cdt = await currentDay();
      setCurrentDay(_cdt);
    } catch (err) {
      //
    }
  }, [balanceOf, getQueenPrice, getCreationRedemption, currentDay]);

  useIntervalRefresh(refresh);

  const [loading, setLoading] = React.useState();
  const handleClaim = React.useCallback(async () => {
    try {
      setLoading(true);
      const tx = await claimQuee();
      await tx?.wait();
    } catch (err) {}
    setLoading(false);
  }, [claimQuee]);

  const [settleLoading, setSettleLoading] = React.useState();
  const handleSettle = React.useCallback(async () => {
    try {
      setSettleLoading(true);
      const tx = await doSettle();
      await tx?.wait();
      setSettleLoading(false);
    } catch (err) {
      setSettleLoading(false);
      alert(err?.data?.message);
    }
  }, [doSettle]);

  //   const handleClaimAndStake = React.useCallback(async () => {
  //     try {
  //       setLoading(true);
  //       const tx = await claimAndStakeQuee();
  //       await tx?.wait();
  //     } catch (err) {}
  //     setLoading(false);
  //   }, [claimAndStakeQuee]);

  const history = useHistory();
  const handleClaimAndStake = React.useCallback(() => {
    history.push(STAKE);
  }, [history]);

  //   const display_ethAmount1 = formatNumber4Loans(ethAmount1, { places: 4 });
  //   const display_queenAmount1 = formatNumber4Loans(queenAmount1, { places: 4 });
  //   const display_ethAmount2 = formatNumber4Loans(ethAmount2, { places: 4 });
  //   const display_queenAmount2 = formatNumber4Loans(queenAmount2, { places: 4 });

  const createdShares = creationRedemption?.createdShares;
  const creatingUnderlying = creationRedemption?.creatingUnderlying;
  const redeemedUnderlying = creationRedemption?.redeemedUnderlying;
  const redeemingShares = creationRedemption?.redeemingShares;

  const currentTimestamp = new Date().getTime() / 1000;
  const settleButtonStatus = currentTimestamp > currentDayTimestamp ? true : false;

  const disabled =
    (!hasString(createdShares) || bigLessThanOrEqualTo(createdShares, 0)) &&
    (!hasString(redeemedUnderlying) || bigLessThanOrEqualTo(redeemedUnderlying, 0));

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%', padding: '20px' }}>
      <Box sx={{ display: 'inline' }}>{`Next settlement is on ${endOfDayPlus15Plus1()}`}</Box>
      <LoadingButton
        loading={settleLoading}
        variant="outlined"
        size="small"
        style={{ margin: '20px 0 0' }}
        disabled={!settleButtonStatus}
        onClick={handleSettle}
      >
        Settle
      </LoadingButton>
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '65px', mt: '20px' }}>
        <h5>
          {`Settled: ${formatNumber4Loans(createdShares, {
            places: 2,
          })} Queen`}
        </h5>
        <h5>
          {`Unsettled: ${formatNumber4Loans(creatingUnderlying, {
            places: 4,
          })} ETH -> ${formatNumber4Loans(mul(creatingUnderlying, queenPrice), {
            places: 2,
          })} Queen`}
        </h5>
      </Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '65px' }}>
        <h5>
          {`Settled: ${formatNumber4Loans(redeemedUnderlying, {
            places: 4,
          })} ETH`}
        </h5>
        <h5>
          {`Unsettled: ${formatNumber4Loans(redeemingShares, {
            places: 2,
          })} Queen -> ${formatNumber4Loans(div(redeemingShares, queenPrice), {
            places: 4,
          })} ETH`}
        </h5>
      </Box>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          width: '100%',
          alignItems: 'center',
          justifyContent: 'space-between',
          mt: '20px',
        }}
      >
        <LoadingButton
          variant="contained"
          size="medium"
          style={{ minWidth: '120px' }}
          disabled={disabled}
          loading={loading}
          onClick={handleClaim}
        >
          Claim
        </LoadingButton>
        <LoadingButton
          variant="contained"
          size="medium"
          style={{ marginLeft: '10px' }}
          disabled={!hasString(balance)}
          onClick={handleClaimAndStake}
        >
          Stake Queen
        </LoadingButton>
      </Box>
      <Box sx={{ display: 'inline', mt: '20px' }}>
        <h5>Stake Queens to earn ACHESS reward without lockup period</h5>
      </Box>
    </Box>
  );
});

export default function BasicTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{
        width: '100%',
        flexDirection: 'column',
        mt: '10px',
      }}
    >
      <Box
        sx={{
          borderBottom: 1,
          borderColor: 'divider',
        }}
      >
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="Claimable assets" {...a11yProps(0)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Claimable />
      </TabPanel>
    </Box>
  );
}
