import React from 'react';

export const Stepper = props => {
  const { step = 0.25, min = -10, max = 10, defaultValue = 2 } = props ?? {};

  const [value, setValue] = React.useState(defaultValue);

  const handleDecrease = React.useCallback(() => {
    const _value = value;
    if (_value - step <= min) {
      setValue(min);
    } else {
      setValue(_value - step);
    }
  }, [value, step, min]);
  const handleIncrease = React.useCallback(() => {
    const _value = value;
    if (_value + step >= max) {
      setValue(max);
    } else {
      setValue(_value + step);
    }
  }, [value, step, max]);

  return (
    <div style={{ display: 'flex', flexFlow: 'row nowrap', width: '100px' }}>
      <button onClick={handleDecrease}>-</button>
      <div
        style={{ display: 'flex', flexFlow: 'row nowrap', flex: '1 0 auto', justifyContent: 'center' }}
      >{`${value}%`}</div>
      <button onClick={handleIncrease}>+</button>
    </div>
  );
};
