import React from 'react';
import 'App.css';
import Button from '@mui/material/Button';
import { QUEEN, ROOT, STAKE, TRADE } from 'router/CONSTANTS';
import { useHistory } from 'react-router-dom';
import { formatNumber4Loans, formatPercentages4Loans } from 'utils/format';
import { hasString } from 'utils/helper';
import WalletModel from 'wallet';
import { useIntervalRefresh } from 'wallet/tools';
import { STAKETOKENS } from 'wallet/stakeTokens';
import { POOLS } from 'wallet/pools';
import { mul, plus, div } from 'utils/operation';

const PoolsView = props => {
  const { data } = props ?? {};

  const buttonsStyle = {
    width: '90%',
    margin: '8px 0 8px 0',
    padding: '8px 0 8px 0',
    textTransform: 'none !important',
  };

  const history = useHistory();
  const goTo = path => {
    history.push(path || ROOT);
  };

  const { apr, getSharedStakingAPR, getLendingApr, getNav } = WalletModel.useContext();

  const [lpRate, setLpRate] = React.useState();
  const [queenRate, setQueenRate] = React.useState();
  const [bishopRate, setBishopRate] = React.useState();
  const [leverage, setLeverage] = React.useState();

  // const poolLength = POOLS.length;
  // const lpAprIndex = poolLength - 1;

  const refresh = React.useCallback(async () => {
    try {
      const _lpRate = await apr(POOLS[0].contract, POOLS[0].name);
      setLpRate(_lpRate);
      const _queenRate = await getSharedStakingAPR(STAKETOKENS[0].contract, 0);
      setQueenRate(_queenRate);
      const _bishopRate = await getSharedStakingAPR(STAKETOKENS[1].contract, 1);
      const _lendingRate = await getLendingApr();
      setBishopRate(plus(_bishopRate, _lendingRate));
      const nav = await getNav();
      setLeverage(div(plus(nav?.a, nav?.b), nav?.b));
    } catch (err) {
      // console.log(err);
    }
  }, [apr, getSharedStakingAPR, getLendingApr, getNav]);

  useIntervalRefresh(refresh);

  let rate;

  switch (data?.token) {
    case 'Queen':
      rate = mul(queenRate, 365);
      break;
    case 'ACHESS-USDC SLP':
      rate = mul(lpRate?.apr, 365);
      break;
    case 'Bishop':
      rate = mul(bishopRate, 365);
      break;
    default:
      break;
  }

  return (
    <div className="pools-container common-bg">
      <div className="boxes">
        <div className="miner-items miner-items1">
          <h1>{data?.token}</h1>
          <h4>{data?.des}</h4>
        </div>
        <div className="miner-items miner-items2">
          <h5>{data?.rateName}</h5>
          <h3>
            {data?.token === 'Rook' ? `${formatNumber4Loans(leverage, { places: 2 })}x` : formatPercentages4Loans(rate)}
          </h3>
        </div>
        <div className="miner-items miner-items3">
          {data?.button1?.display ? (
            <Button
              variant="contained"
              size="small"
              sx={buttonsStyle}
              disabled={data?.button1?.disabled}
              onClick={() => (hasString(data?.button1?.router) ? goTo(data?.button1?.router) : goTo(QUEEN))}
            >
              {data?.button1?.text}
            </Button>
          ) : null}
          {data?.button2?.display ? (
            <Button
              variant="contained"
              size="small"
              sx={buttonsStyle}
              disabled={data?.button2?.disabled}
              onClick={() => (hasString(data?.button2?.router) ? goTo(data?.button2?.router) : goTo(STAKE))}
            >
              {data?.button2?.text}
            </Button>
          ) : null}
          {data?.button3?.display ? (
            <Button
              variant="contained"
              size="small"
              sx={buttonsStyle}
              disabled={data?.button3?.disabled}
              onClick={() =>
                hasString(data?.button3?.router) ? goTo(data?.button3?.router) : goTo({ pathname: TRADE, state: data })
              }
            >
              {data?.button3?.text}
            </Button>
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default PoolsView;
