import React from 'react';
import _ from 'lodash';
import { Box } from '@mui/system';
// import {NavLinks} from 'components/NavLinks';
import 'App.css';
import ImgLogo from 'assets/logo.png';
import NavMenus from './NavMenus';
import { ROOT } from 'router/CONSTANTS';
import Button from '@mui/material/Button';
import WalletModel from 'wallet';
import { hasString } from 'utils/helper';
import { ellipsizeText } from 'utils/input';
import { getAddress } from 'utils/store';

const AutoConnect = React.memo(props => {
  const { connect } = WalletModel.useContext();
  if (hasString(getAddress())) {
    connect();
  }
  return null;
});

function Header() {
  const TARGET_CHAINID = [42161];
  const { connect, address, network } = WalletModel.useContext();

  const handleWalletClick = React.useCallback(() => {
    if (!hasString(address)) {
      connect();
    }
  }, [connect, address]);

  let text = 'Connect';
  if (hasString(address)) {
    if (_.includes(TARGET_CHAINID, network?.chainId)) {
      text = ellipsizeText(address, 8, 'middle');
    } else {
      text = 'Wrong Net';
    }
  }

  return (
    <Box sx={{ display: 'flex' }}>
      <AutoConnect />
      <div className="header">
        <a style={{ cursor: 'pointer' }} href={ROOT}>
          <img className="logoImg" src={ImgLogo} alt="logo" />
        </a>
        <Box sx={{ display: 'flex' }}>
          <Button variant="outlined" size="small" onClick={handleWalletClick}>
            {text}
          </Button>
          <NavMenus />
        </Box>
      </div>
      {/*<NavLinks/>*/}
    </Box>
  );
}

export default React.memo(Header);
