import * as React from 'react';
import _ from 'lodash';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import LoadingButton from '@mui/lab/LoadingButton';
import WalletModel from 'wallet';
import { useIntervalRefresh } from 'wallet/tools';
import { formatNumber4Loans } from 'utils/format';
import { getValue, hasString, hasValue } from 'utils/helper';
import { formatNumberInputValue } from 'utils/input';
import { bigGreaterThan, mul, div, sub, safeMinV2, BLANK_CHAR } from 'utils/operation';
import { QUEEN, BISHOP, ROOK } from 'wallet/wallet';

export default function InputAdornments(props) {
  const { label } = props ?? {};

  const isSplit = label === 'Split Queen';

  const { balanceOf, split, merge } = WalletModel.useContext();

  const [balanceM, setBalanceM] = React.useState();
  const [balanceA, setBalanceA] = React.useState();
  const [balanceB, setBalanceB] = React.useState();
  const [amount, setAmount] = React.useState();

  const [values, setValues] = React.useState({
    amount: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });

  const handleChange = React.useCallback(
    prop => event => {
      const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
      const value = formatNumberInputValue(newValue, 18);
      setValues({ weight: value });
      setAmount(value);
    },
    []
  );

  const refresh = React.useCallback(async () => {
    try {
      if (isSplit) {
        const bM = await balanceOf(QUEEN);
        setBalanceM(bM);
      } else {
        const bA = await balanceOf(BISHOP);
        setBalanceA(bA);
        const bB = await balanceOf(ROOK);
        setBalanceB(bB);
      }
    } catch (err) {
      //
    }
  }, [isSplit, balanceOf]);

  useIntervalRefresh(refresh);

  const [loading, setLoading] = React.useState();
  const handleClick = React.useCallback(async () => {
    try {
      setLoading(true);
      if (isSplit) {
        const tx = await split(amount);
        await tx?.wait();
      } else {
        const tx = await merge(amount);
        await tx?.wait();
      }
      setValues({ weight: '' });
      setAmount('');
    } catch (err) {}
    setLoading(false);
  }, [isSplit, split, merge, amount]);

  const handleMAX = React.useCallback(() => {
    let _balance;
    if (isSplit) {
      _balance = balanceM;
    } else {
      _balance = safeMinV2(balanceA, balanceB);
    }
    setValues({ weight: _balance });
    setAmount(_balance);
  }, [isSplit, balanceM, balanceA, balanceB]);

  const toAmount = isSplit ? mul(div(amount, 2), sub(1, '0.0005')) : mul(mul(amount, 2), sub(1, '0.0005'));

  let disabled = false;
  if (isSplit) {
    disabled =
      !hasString(amount) || bigGreaterThan(0, amount) || !hasValue(balanceM) || bigGreaterThan(amount, balanceM);
  } else {
    disabled =
      !hasString(amount) ||
      bigGreaterThan(0, amount) ||
      !hasValue(balanceA) ||
      bigGreaterThan(amount, balanceA) ||
      !hasValue(balanceB) ||
      bigGreaterThan(amount, balanceB);
  }

  if (!hasString(label)) {
    return <div />;
  }

  return (
    <Box
      sx={{
        display: 'flex',
        flexWrap: 'wrap',
        paddingTop: '20px',
      }}
    >
      <div>
        <FormControl sx={{ m: 1, width: '25ch' }} variant="outlined">
          <OutlinedInput
            id="outlined-adornment-weight"
            value={values.weight}
            onChange={handleChange('weight')}
            placeholder={`${isSplit ? 'Queen' : 'Bishop & Rook'}`}
            aria-describedby="outlined-weight-helper-text"
            inputProps={{
              'aria-label': 'weight',
            }}
          />
          <div style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
            {isSplit ? (
              <h5>{`Wallet: ${formatNumber4Loans(balanceM, { places: 2 })} Queen`}</h5>
            ) : (
              <div style={{ flexDirection: 'column' }}>
                <h5>{`Wallet: ${formatNumber4Loans(balanceA, { places: 2 })} Bishop`}</h5>
                <h5>{`Wallet: ${formatNumber4Loans(balanceB, { places: 2 })} Rook`}</h5>
              </div>
            )}
            <span style={{ cursor: 'pointer' }} onClick={handleMAX}>
              <h5>MAX</h5>
            </span>
          </div>
          <div>
            <h5>Fee: 0.05%</h5>
          </div>
          <div style={{ justifyContent: 'center', padding: '10px 0' }}>
            <ArrowDownwardIcon />
          </div>
          <div>
            <h3>{`${formatNumber4Loans(toAmount, { places: 2 }) ?? BLANK_CHAR} ${
              isSplit ? 'Bishop & Rook' : 'Queen'
            }`}</h3>
          </div>
          <LoadingButton
            variant="contained"
            size="medium"
            style={{ margin: '20px 0' }}
            disabled={disabled}
            loading={loading}
            onClick={handleClick}
          >
            {label}
          </LoadingButton>
        </FormControl>
      </div>
    </Box>
  );
}
