// 时间日期格式
export const DateFormat = {
  DATE: 'YYYY-MM-DD',
  SHORT_DATE: 'MM-DD',
  LOCALE_DATE: 'LLL',
  TIME: 'HH:mm:ss',
  SHORT_TIME: 'HH:mm',
  DATE_AND_TIME: 'YYYY-MM-DD HH:mm:ss',
  SHORT_DATE_AND_TIME: 'MM-DD HH:mm:ss',
};

export function str2int(value) {
  if (isBlankChar(value)) {
    return BLANK_CHAR;
  }

  if (!hasValue(value)) {
    return 0;
  }

  const val = `${value}`;
  if (!hasString(val)) {
    return 0;
  }
  const res = parseInt(val, 10);
  return _.isNaN(res) ? 0 : res;
}

export function str2float(value) {
  if (isBlankChar(value)) {
    return BLANK_CHAR;
  }

  if (!hasValue(value)) {
    return 0;
  }

  const val = `${value}`;
  if (!hasString(val)) {
    return 0;
  }
  const res = parseFloat(val);
  return _.isNaN(res) ? 0 : res;
}

