import React from 'react';
import 'App.css';
import { useIntervalRefresh } from 'wallet/tools';
import WalletModel from 'wallet';
import { plus } from 'utils/operation';
import { formatCurrency4Loans, formatNumber4Loans } from 'utils/format';
import { POOLS } from 'wallet/pools';

export const TARGET_CHAINID = [42161];

const TvlView = props => {
  const { getLPTVL, getETHTVL } = WalletModel.useContext();

  const [lpTvl, setLpTvl] = React.useState();
  const [ethTvl, setEthTvl] = React.useState();

  const refresh = React.useCallback(async () => {
    try {
      const _lpTvl = await getLPTVL(POOLS[0].contract, POOLS[0].name);
      setLpTvl(_lpTvl);
      const _ethTvl = await getETHTVL();
      setEthTvl(_ethTvl);
      // console.log(_lpTvl, _ethTvl);
    } catch (err) {
      // console.log(err);
    }
  }, [getETHTVL, getLPTVL]);

  useIntervalRefresh(refresh);

  const totalUSD = plus(lpTvl?.tvlUSD ?? 0, ethTvl?.tvlUSD ?? 0);

  return (
    <div className="intro-container">
      <div className="tvl-box">
        <div className="intro-item">
          <h1>Yield Enhancing Asset Tracker With Varied Risk-Return Solutions</h1>
        </div>
        <div className="tvl-items">
          <div className="tvl-item">
            <h4>TOTAL VALUE LOCKED</h4>
            <h1 className="blue-font">{formatCurrency4Loans(totalUSD)}</h1>
          </div>
          <div className="tvl-item">
            <h4>TOTAL ETH DEPOSITED</h4>
            <h1 className="blue-font">{formatNumber4Loans(ethTvl?.tvlETH, { places: 4 })} Ξ</h1>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TvlView;
