import _ from 'lodash';
import React from 'react';
import { ethers as Ethers } from 'ethers';

import { createHookContext, useModel } from '../utils/hooks';
import { hasArray, hasString, hasValue } from '../utils/helper';
import { setAddress as _setAddress } from '../utils/store';
import { waitForSth } from '../utils/tools';
import { MAX_UINT256, ETH_TOKEN_ADDRESS, ETH_TOKEN_DECIMALS, calcGasLimit } from './tools';

import ERC20Abi from './abi/ERC20.json';
import AChessRewardsAbi from './abi/AChessRewards.json';
import ChainLinkOracleAbi from './abi/EACAggregatorProxy.json';
import UniswapV2Router02Abi from './abi/UniswapV2Router02.json';
import FundAbi from './abi/Fund.json';
import PrimaryMarketAbi from './abi/PrimaryMarket.json';
import StakingAbi from './abi/Staking.json';
import ChessScheduleAbi from './abi/ChessSchedule.json';
import LPAChessRewardsAbi from './abi/LPAChessRewards.json';

import { div, mul, plus, safeEq, sub } from '../utils/operation';
import { POOLS } from './pools';
import { endOfDaySub1, endOfWeek } from 'utils/format';

import MerkleTree from 'utils/merkleTree';
import airdrop from 'utils/airdrop.json';

export async function newWeb3Provider(props = {}) {
  let web3Provider;
  const ethereum = await waitForSth(() => window.ethereum, 30);
  // console.log(ethereum.on);
  web3Provider = new Ethers.providers.Web3Provider(ethereum);
  return web3Provider;
}

export const CHAIN_LINK_ORACLE = {
  ETH: '0x639Fe6ab55C921f74e7fac1ee960C0B6293ba612',
  BTC: '0x6ce185860a4963106506C203335A2910413708e9',
};

export const ACHESS = '0xd713d9914F6730246C900C5ca0a8752959367A30';
export const SUSHI_ROUTER = '0x1b02dA8Cb0d097eB8D57A175b88c7D8b47997506';
export const SUSHI_USDC = '0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8';
export const SUSHI_WETH = '0x82aF49447D8a07e3bd95BD0d56f35241523fBab1';
export const NYAN = '0xeD3fB761414DA74b74F33e5c5a1f78104b188DfC';

export const FUND = '0x81D851800d097faFb9844Cc7f88C6a055989fe3A';
export const PRIMARY_MARKET = '0xB414172344eed805422D0a69900D91Efc57731EB';
export const QUEEN = '0x6e0032D318f5A3A9071e3ed37d795C873AD9442b';
export const BISHOP = '0x61F865D1CCB293B0C63a587bF33EAA8B922e8D21';
export const ROOK = '0x3674702A425488c6F015Ee815a183A8543f6942a';
export const STAKING = '0x6cFC629F15A6c2368Bbe63b1060c48679eaA2024';
export const BETASTAKING = '0xAc8328450621431A1645F9DF319A702bf46CE0fB'; // for old Queen pool, no reward
export const CHESS_SCHEDULE = '0x6172b18930F56e54b5F51Da6cF7b2a552ee1E524';

function useWalletModel(props = {}) {
  const ethersProvider = React.useRef();
  const [network, setNetwork] = React.useState();
  const [address, setAddress] = React.useState();

  const disconnect = React.useCallback(async () => {
    setAddress(null);
    _setAddress('');
  }, []);

  const handleAccountsChanged = React.useCallback(async accounts => {
    setAddress(accounts[0]);
  }, []);

  const handleDisconnect = React.useCallback(async () => {
    await disconnect();
  }, [disconnect]);

  const handleReload = React.useCallback(async () => {
    window.location.reload();
  }, []);

  const install = React.useCallback(async () => {
    let web3Provider = ethersProvider.current;

    if (hasValue(web3Provider?.provider?.off)) {
      web3Provider.provider.off('chainChanged', handleReload);
      web3Provider.provider.off('accountsChanged', handleAccountsChanged);
      web3Provider.provider.off('disconnect', handleDisconnect);
    }

    web3Provider = await newWeb3Provider();

    web3Provider.provider.on('chainChanged', handleReload);
    web3Provider.provider.on('accountsChanged', handleAccountsChanged);
    web3Provider.provider.on('disconnect', handleDisconnect);

    // const { chainId, name } = await web3Provider.getNetwork();
    // setNetwork({ chainId, name });

    // const accounts = await web3Provider.listAccounts();
    // setAddress(accounts[0]);

    ethersProvider.current = web3Provider;
  }, [handleAccountsChanged, handleDisconnect, handleReload]);

  const connect = React.useCallback(async () => {
    ethersProvider.current = null;
    await install();
    const ethereum = await waitForSth(() => ethersProvider.current, 30);
    await ethereum.provider.enable();
    const { chainId, name } = await ethereum?.getNetwork();
    setNetwork({ chainId, name });
    const accounts = await ethereum?.listAccounts();
    setAddress(accounts[0]);
    _setAddress(accounts[0]);
  }, [install]);

  const allowance = React.useCallback(async (tokenAddress, spenderAddress) => {
    if (!hasString(spenderAddress)) {
      return;
    }
    if (!hasString(tokenAddress) || tokenAddress === ETH_TOKEN_ADDRESS) {
      return MAX_UINT256;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();

    const contract = new Ethers.Contract(tokenAddress, ERC20Abi, signer);
    const allowance = await contract.allowance(address, spenderAddress);

    const decimals = await contract.decimals();
    const response = Ethers.utils.formatUnits(allowance, decimals);
    return response;
  }, []);

  const approve = React.useCallback(async (tokenAddress, spenderAddress, value) => {
    if (!hasString(tokenAddress)) {
      return;
    }
    if (!hasString(spenderAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(tokenAddress, ERC20Abi, signer);
    const gasLimit = await contract.estimateGas.approve(spenderAddress, value);

    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const response = await contract.approve(spenderAddress, value, overrides);
    return response;
  }, []);

  const balanceOf = React.useCallback(async tokenAddress => {
    let balance = null;
    let decimals = ETH_TOKEN_DECIMALS;

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();

    if (tokenAddress === ETH_TOKEN_ADDRESS) {
      balance = await signer.getBalance('latest');
    } else {
      const contract = new Ethers.Contract(tokenAddress, ERC20Abi, signer);
      balance = await contract.balanceOf(address);
      decimals = await contract.decimals();
    }
    const response = Ethers.utils.formatUnits(balance, decimals);
    return response;
  }, []);

  const stakeBalanceOf = React.useCallback(async contractAddress => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const stakeToken = await contract.stakedToken();
    const stakeTokenContract = new Ethers.Contract(stakeToken, ERC20Abi, signer);

    let decimals = ETH_TOKEN_DECIMALS;
    if (hasString(stakeToken) && stakeToken !== ETH_TOKEN_ADDRESS) {
      decimals = await stakeTokenContract.decimals();
    }
    const balance = await contract.balanceOf(address);
    const response = Ethers.utils.formatUnits(balance, decimals);
    return response;
  }, []);

  const stakeTokenInfo = React.useCallback(
    async contractAddress => {
      if (!hasString(contractAddress)) {
        return;
      }

      if (!hasValue(ethersProvider.current)) {
        return;
      }
      const signer = await ethersProvider.current?.getSigner();
      const address = await signer.getAddress();

      const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

      const stakeToken = await contract.stakedToken();
      const _stakeBalance = await contract.balanceOf(address);
      const _totalStaked = await contract.totalSupply();

      const stakeTokenContract = new Ethers.Contract(stakeToken, ERC20Abi, signer);

      let symbol = 'ETH';
      let decimals = ETH_TOKEN_DECIMALS;
      if (hasString(stakeToken) && stakeToken !== ETH_TOKEN_ADDRESS) {
        symbol = await stakeTokenContract.symbol();
        decimals = await stakeTokenContract.decimals();
      }

      const walletBalance = await balanceOf(stakeToken);
      const _allowance = await allowance(stakeToken, contractAddress);

      const stakeBalance = Ethers.utils.formatUnits(_stakeBalance, decimals);
      const totalStaked = Ethers.utils.formatUnits(_totalStaked, decimals);

      return {
        address: stakeToken,
        symbol,
        decimals,
        stakeBalance,
        totalStaked,
        walletBalance,
        allowance: _allowance,
      };
    },
    [balanceOf, allowance]
  );

  const stake = React.useCallback(async (contractAddress, value) => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const stakeToken = await contract.stakedToken();
    const stakeTokenContract = new Ethers.Contract(stakeToken, ERC20Abi, signer);

    let decimals = ETH_TOKEN_DECIMALS;

    if (hasString(stakeToken) && stakeToken !== ETH_TOKEN_ADDRESS) {
      decimals = await stakeTokenContract.decimals();
    }
    const gasLimit = await contract.estimateGas.stake(Ethers.utils.parseUnits(value, decimals));

    let _value = 0;

    if (!hasString(stakeToken) || stakeToken === ETH_TOKEN_ADDRESS) {
      _value = Ethers.utils.parseUnits(value, ETH_TOKEN_DECIMALS);
    }

    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
      value: _value,
    };
    const result = await contract.stake(Ethers.utils.parseUnits(value, decimals), overrides);
    return result;
  }, []);

  const withdraw = React.useCallback(async (contractAddress, value) => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const stakeToken = await contract.stakedToken();
    const stakeTokenContract = new Ethers.Contract(stakeToken, ERC20Abi, signer);

    let decimals = ETH_TOKEN_DECIMALS;

    if (hasString(stakeToken) && stakeToken !== ETH_TOKEN_ADDRESS) {
      decimals = await stakeTokenContract.decimals();
    }
    const gasLimit = await contract.estimateGas.withdraw(Ethers.utils.parseUnits(value, decimals));

    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.withdraw(Ethers.utils.parseUnits(value, decimals), overrides);
    return result;
  }, []);

  const earned = React.useCallback(async contractAddress => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const rewardToken = await contract.rewardToken();
    const rewardTokenContract = new Ethers.Contract(rewardToken, ERC20Abi, signer);

    let decimals = ETH_TOKEN_DECIMALS;

    if (hasString(rewardToken) && rewardToken !== ETH_TOKEN_ADDRESS) {
      decimals = await rewardTokenContract.decimals();
    }

    const result = await contract.earned(address);

    return Ethers.utils.formatUnits(result, decimals);
  }, []);

  const claim = React.useCallback(async contractAddress => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const gasLimit = await contract.estimateGas.claim();

    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.claim(overrides);

    return result;
  }, []);

  const totalClaimed = React.useCallback(async contractAddress => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();

    const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

    const rewardToken = await contract.rewardToken();
    const rewardTokenContract = new Ethers.Contract(rewardToken, ERC20Abi, signer);

    let decimals = ETH_TOKEN_DECIMALS;

    if (hasString(rewardToken) && rewardToken !== ETH_TOKEN_ADDRESS) {
      decimals = await rewardTokenContract.decimals();
    }

    const result = await contract.userClaimed(address);

    return Ethers.utils.formatUnits(result, decimals);
  }, []);

  const chainLinkPrice = React.useCallback(async oracle => {
    if (!hasString(oracle)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(oracle, ChainLinkOracleAbi, signer);

    const [decimals, latestAnswer] = await Promise.all([await contract.decimals(), await contract.latestAnswer()]);
    const result = Ethers.utils.formatUnits(latestAnswer, decimals);

    return result;
  }, []);

  const sushiPrice = React.useCallback(async path => {
    if (!hasArray(path)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const fromAddress = path[0];
    const toAddress = path[1];
    const from = new Ethers.Contract(fromAddress, ERC20Abi, signer);
    const to = new Ethers.Contract(toAddress, ERC20Abi, signer);
    let fromDecimals = ETH_TOKEN_DECIMALS;
    if (hasString(fromAddress) && fromAddress !== ETH_TOKEN_ADDRESS) {
      fromDecimals = await from.decimals();
    }
    let toDecimals = ETH_TOKEN_DECIMALS;
    if (hasString(toAddress) && toAddress !== ETH_TOKEN_ADDRESS) {
      toDecimals = await to.decimals();
    }
    const contract = new Ethers.Contract(SUSHI_ROUTER, UniswapV2Router02Abi, signer);
    const price = await contract.getAmountsOut(Ethers.utils.parseUnits('1', fromDecimals), path);
    const result = Ethers.utils.formatUnits(price[1], toDecimals);

    return result;
  }, []);

  const lpPriceSimple = React.useCallback(async lpAddress => {
    if (!hasString(lpAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(lpAddress, ERC20Abi, signer);
    let decimals = ETH_TOKEN_DECIMALS;
    if (hasString(lpAddress) && lpAddress !== ETH_TOKEN_ADDRESS) {
      decimals = await contract.decimals();
    }

    const _totalSupply = await contract.totalSupply();
    const totalSupply = Ethers.utils.formatUnits(_totalSupply, decimals);

    const usdContract = new Ethers.Contract(SUSHI_USDC, ERC20Abi, signer);
    let usdDecimals = await usdContract.decimals();
    const _balance = await usdContract.balanceOf(lpAddress);
    const balance = Ethers.utils.formatUnits(_balance, usdDecimals);

    const result = div(mul(balance, 2), totalSupply);
    return result;
  }, []);

  const apr = React.useCallback(
    async (contractAddress, type) => {
      if (!hasString(contractAddress)) {
        return;
      }

      if (!hasValue(ethersProvider.current)) {
        return;
      }
      const signer = await ethersProvider.current?.getSigner();
      // const address = await signer.getAddress();

      const contract = new Ethers.Contract(contractAddress, AChessRewardsAbi, signer);

      const rate = await contract.rewardRate();
      const rewardToken = await contract.rewardToken();

      const _totalSupply = await contract.totalSupply();
      const stakeToken = await contract.stakedToken();
      const stakeTokenContract = new Ethers.Contract(stakeToken, ERC20Abi, signer);

      let decimals = ETH_TOKEN_DECIMALS;
      if (hasString(stakeToken) && stakeToken !== ETH_TOKEN_ADDRESS) {
        decimals = await stakeTokenContract.decimals();
      }
      const totalSupply = Ethers.utils.formatUnits(_totalSupply, decimals);
      const chessPrice = await sushiPrice([rewardToken, SUSHI_USDC]);

      let lockedPrice;
      switch (type) {
        case 'ETH':
          lockedPrice = await chainLinkPrice(CHAIN_LINK_ORACLE.ETH);
          break;
        case 'ETH-USDC SushiSwap LP':
          lockedPrice = await lpPriceSimple(stakeToken);
          break;
        case 'NYAN':
          lockedPrice = mul(await sushiPrice([NYAN, SUSHI_WETH]), await chainLinkPrice(CHAIN_LINK_ORACLE.ETH));
          break;
        case 'pETH':
          lockedPrice = div(await chainLinkPrice(CHAIN_LINK_ORACLE.ETH), 50);
          break;
        case 'WBTC':
          lockedPrice = await chainLinkPrice(CHAIN_LINK_ORACLE.BTC);
          break;
        case 'ACHESS-USDC SUSHI LP':
          lockedPrice = await lpPriceSimple(stakeToken);
          break;
        default:
          break;
      }

      // ((rate*24*60*60/1e18)*price1/totalSupply*price2)*365

      const _rate = div(mul(rate.toString(), 24 * 60 * 60), 1e18);
      const locked = mul(totalSupply, lockedPrice);
      const result = div(mul(_rate, chessPrice), locked);
      // console.log(type, _rate, chessPrice, totalSupply, lockedPrice, result);

      return {
        rate: _rate,
        apr: result,
        locked,
      };
    },
    [sushiPrice, chainLinkPrice, lpPriceSimple]
  );

  const tvl = React.useCallback(async () => {
    const result = await Promise.all(_.map(POOLS, async pool => await apr(pool.contract, pool.name)));
    let tvlUSD = 0;
    _.forEach(result ?? [], item => {
      tvlUSD = plus(tvlUSD, item.locked);
    });
    const ethPrice = await chainLinkPrice(CHAIN_LINK_ORACLE.ETH);
    return {
      tvlUSD,
      tvlETH: div(tvlUSD, ethPrice),
    };
  }, [apr, chainLinkPrice]);

  const chessPrice = React.useCallback(async () => {
    const chessPrice = await sushiPrice([ACHESS, SUSHI_USDC]);
    return chessPrice;
  }, [sushiPrice]);

  const getQueenPrice = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(FUND, FundAbi, signer);
    let decimals = ETH_TOKEN_DECIMALS;
    const _totalShares = await contract.getTotalShares();
    const totalShares = Ethers.utils.formatUnits(_totalShares, decimals);

    const _balance = await ethersProvider.current.getBalance(FUND);
    const balance = Ethers.utils.formatUnits(_balance, decimals);

    const _dailyProtocolFeeRate = await contract.dailyProtocolFeeRate();
    const dailyProtocolFeeRate = Ethers.utils.formatUnits(_dailyProtocolFeeRate, 18);

    // const balance = await fundContract.signer.getBalance('latest');

    // const primaryMarketContract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);

    const result = div(totalShares, mul(balance, sub(1, dailyProtocolFeeRate)));
    return result;
  }, []);

  const create = React.useCallback(async amount => {
    if (!hasString(amount)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const address = await signer.getAddress();
    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);

    const value = Ethers.utils.parseUnits(amount, ETH_TOKEN_DECIMALS);

    const tree = new MerkleTree(airdrop);
    // console.log(tree.getHexRoot());
    const proof = tree.getHexProof(address);
    const gasLimit = await contract.estimateGas.create(0, proof, { value: value });
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
      value: value,
    };
    const result = await contract.create(0, proof, overrides);

    return result;
  }, []);

  const redeem = React.useCallback(async amount => {
    if (!hasString(amount)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);
    const value = Ethers.utils.parseUnits(amount, 18);
    const gasLimit = await contract.estimateGas.redeem(value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.redeem(value, overrides);

    return result;
  }, []);

  const claimQuee = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);
    const address = await signer.getAddress();
    const gasLimit = await contract.estimateGas.claim(address);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.claim(address, overrides);

    return result;
  }, []);

  const claimAndStakeQuee = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const gasLimit = await contract.estimateGas.claimAndDeposit(PRIMARY_MARKET);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.claimAndDeposit(PRIMARY_MARKET, overrides);

    return result;
  }, []);

  const stakeShare = React.useCallback(async (tranche, amount) => {
    // tranche M Queen = 0 A Bishop = 1 B Rook= 2
    if (!hasString(amount)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const value = Ethers.utils.parseUnits(amount, 18);
    const gasLimit = await contract.estimateGas.deposit(tranche, value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.deposit(tranche, value, overrides);

    return result;
  }, []);

  const withdrawShare = React.useCallback(async (tranche, amount) => {
    if (!hasString(amount)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const value = Ethers.utils.parseUnits(amount, 18);
    const gasLimit = await contract.estimateGas.withdraw(tranche, value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.withdraw(tranche, value, overrides);

    return result;
  }, []);

  const withdrawBetaShare = React.useCallback(async (tranche, amount) => {
    if (!hasString(amount)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(BETASTAKING, StakingAbi, signer);
    const value = Ethers.utils.parseUnits(amount, 18);
    const gasLimit = await contract.estimateGas.withdraw(tranche, value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.withdraw(tranche, value, overrides);

    return result;
  }, []);

  const claimableRewards = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const address = await signer.getAddress();

    const _result = await contract.callStatic.claimableRewards(address);
    const result = Ethers.utils.formatUnits(_result, 18);

    return result;
  }, []);

  const claimRewards = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const address = await signer.getAddress();
    const gasLimit = await contract.estimateGas.claimRewards(address);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.claimRewards(address, overrides);

    return result;
  }, []);

  const getCreationRedemption = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);
    const address = await signer.getAddress();

    const result = await contract.creationRedemptionOf(address);

    return {
      creatingUnderlying: Ethers.utils.formatUnits(result[1], 18),
      redeemingShares: Ethers.utils.formatUnits(result[2], 18),
      createdShares: Ethers.utils.formatUnits(result[3], 18),
      redeemedUnderlying: Ethers.utils.formatUnits(result[4], 18),
    };
  }, []);

  const getStakingBalance = React.useCallback(async tranche => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(STAKING, StakingAbi, signer);
    const address = await signer.getAddress();

    const _result = await contract.availableBalanceOf(tranche, address);

    const result = Ethers.utils.formatUnits(_result, 18);

    return result;
  }, []);

  const getBetaStakingBalance = React.useCallback(async tranche => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(BETASTAKING, StakingAbi, signer);
    const address = await signer.getAddress();

    const _result = await contract.availableBalanceOf(tranche, address);

    const result = Ethers.utils.formatUnits(_result, 18);
    console.log(result);
    return result;
  }, []);

  const getNav = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(FUND, FundAbi, signer);

    const time = endOfDaySub1(new Date().getTime() / 1000);

    const _result = await contract.historicalNavs(time);

    const result = {
      m: Ethers.utils.formatUnits(_result[0], 18),
      a: Ethers.utils.formatUnits(_result[1], 18),
      b: Ethers.utils.formatUnits(_result[2], 18),
    };

    return result;
  }, []);

  const getSharedTotalStake = React.useCallback(async tokenAddress => {
    if (!hasString(tokenAddress)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(tokenAddress, ERC20Abi, signer);

    const _result = await contract.balanceOf(STAKING);

    const result = Ethers.utils.formatUnits(_result, 18);

    return result;
  }, []);

  // 0x505440f55caca775532c36536cf13557dbff9e11

  const addMinter = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(CHESS_SCHEDULE, ChessScheduleAbi, signer);

    const result = await contract.addMinter(STAKING);

    return result;
  }, []);

  const getETHTVL = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const balance1 = await ethersProvider.current.getBalance(FUND);
    const balance2 = await ethersProvider.current.getBalance(PRIMARY_MARKET);

    const result1 = Ethers.utils.formatUnits(balance1, 18);
    const result2 = Ethers.utils.formatUnits(balance2, 18);

    const result = plus(result1, result2);

    const ethPrice = await chainLinkPrice(CHAIN_LINK_ORACLE.ETH);

    return {
      tvlUSD: mul(result, ethPrice),
      tvlETH: result,
    };
  }, [chainLinkPrice]);

  const getLPTVL = React.useCallback(
    async (contract, name) => {
      const result = await apr(contract, name);
      let tvlUSD = result?.locked;
      const ethPrice = await chainLinkPrice(CHAIN_LINK_ORACLE.ETH);
      return {
        tvlUSD,
        tvlETH: div(tvlUSD, ethPrice),
      };
    },
    [apr, chainLinkPrice]
  );

  const getLPReward = React.useCallback(async contractAddress => {
    if (!hasString(contractAddress)) {
      return;
    }

    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(contractAddress, LPAChessRewardsAbi, signer);

    const gasLimit = await contract.estimateGas.getReward();

    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
      // gasPrice: ,
    };
    const result = await contract.getReward(overrides);

    return result;
  }, []);

  const getSharedStakingAPR = React.useCallback(
    async (tokenAddress, tranche) => {
      if (!hasString(tokenAddress)) {
        return;
      }
      if (!hasValue(ethersProvider.current)) {
        return;
      }
      const signer = await ethersProvider.current?.getSigner();
      const contract1 = new Ethers.Contract(QUEEN, ERC20Abi, signer);
      const _locked1 = await contract1.balanceOf(STAKING);
      const locked1 = Ethers.utils.formatUnits(_locked1, 18);

      const contract2 = new Ethers.Contract(BISHOP, ERC20Abi, signer);
      const _locked2 = await contract2.balanceOf(STAKING);
      const locked2 = Ethers.utils.formatUnits(_locked2, 18);

      const contract3 = new Ethers.Contract(ROOK, ERC20Abi, signer);
      const _locked3 = await contract3.balanceOf(STAKING);
      const locked3 = Ethers.utils.formatUnits(_locked3, 18);

      const locked = div(plus(plus(mul(locked1, 3), mul(locked2, 4)), mul(locked3, 2)), 3);

      if (safeEq(locked, 0)) {
        return null;
      }

      const contractStaking = new Ethers.Contract(STAKING, StakingAbi, signer);
      const _rate = await contractStaking.getRate();
      const rate = Ethers.utils.formatUnits(_rate, 18);

      const { m, a, b } = await getNav();

      let nav = 1;
      let weight = 1;
      switch (tranche) {
        case 0:
          nav = m;
          weight = 3;
          break;
        case 1:
          nav = a;
          weight = 4;
          break;
        case 2:
          nav = b;
          weight = 2;
          break;
        default:
          break;
      }

      const _chessPrice = await chessPrice();

      // ((rate*24*60*60/1e18)*price1/totalSupply*price2)*365

      const day = 24 * 60 * 60;
      const dailyRate = mul(rate, day);
      const result = mul(div(mul(dailyRate, _chessPrice), mul(locked, nav)), div(weight, 3));

      return result;
    },
    [getNav, chessPrice]
  );

  const getUserSharedStaking = React.useCallback(
    async tokenList => {
      if (!hasArray(tokenList)) {
        return;
      }
      if (!hasValue(ethersProvider.current)) {
        return;
      }
      const signer = await ethersProvider.current?.getSigner();
      const address = await signer.getAddress();
      const contract = new Ethers.Contract(STAKING, StakingAbi, signer);

      const results = await Promise.all(
        _.map(tokenList, async token => await contract.availableBalanceOf(token.trancheId, address))
      );

      const { m, a, b } = await getNav();

      let locked = 0;
      _.forEach(results, (_locked, index) => {
        let nav = 1;
        switch (tokenList[index].trancheId) {
          case 0:
            nav = m;
            break;
          case 1:
            nav = a;
            break;
          case 2:
            nav = b;
            break;
          default:
            break;
        }
        const available = Ethers.utils.formatUnits(_locked, 18);
        locked = plus(locked, mul(available, nav));
      });

      return locked;
    },
    [getNav]
  );

  const currentDay = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(FUND, FundAbi, signer);
    const _currentDayTimestamp = await contract.currentDay();

    const currentDayTimestamp = Ethers.utils.formatUnits(_currentDayTimestamp, 0);
    return currentDayTimestamp;
  }, []);

  const doSettle = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(FUND, FundAbi, signer);
    const gasLimit = await contract.estimateGas.settle();
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
    };
    const result = await contract.settle(overrides);
    return result;
  }, []);

  const split = React.useCallback(async inM => {
    if (!hasValue(inM)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);
    const value = Ethers.utils.parseUnits(inM, 18);

    const gasLimit = await contract.estimateGas.split(value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
    };
    const result = await contract.split(value, overrides);
    return result;
  }, []);

  const merge = React.useCallback(async inA => {
    if (!hasValue(inA)) {
      return;
    }
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();

    const contract = new Ethers.Contract(PRIMARY_MARKET, PrimaryMarketAbi, signer);
    const value = Ethers.utils.parseUnits(inA, 18);

    const gasLimit = await contract.estimateGas.merge(value);
    const overrides = {
      gasLimit: calcGasLimit(gasLimit),
    };
    const result = await contract.merge(value, overrides);
    return result;
  }, []);

  const getLendingApr = React.useCallback(async () => {
    if (!hasValue(ethersProvider.current)) {
      return;
    }
    const signer = await ethersProvider.current?.getSigner();
    const contract = new Ethers.Contract(FUND, FundAbi, signer);
    const time = endOfWeek(new Date().getTime() / 1000);
    const _rate = await contract.historicalInterestRate(time);
    const rate = Ethers.utils.formatUnits(_rate, 18);
    // console.log(rate);
    return rate;
  }, []);

  return useModel({
    connect,
    disconnect,
    network,
    address,
    allowance,
    approve,
    balanceOf,
    stakeBalanceOf,
    stakeTokenInfo,
    stake,
    withdraw,
    earned,
    claim,
    totalClaimed,
    chainLinkPrice,
    apr,
    chessPrice,
    tvl,
    getQueenPrice,
    create,
    redeem,
    claimQuee,
    claimAndStakeQuee,
    stakeShare,
    withdrawShare,
    claimableRewards,
    claimRewards,
    getCreationRedemption,
    getStakingBalance,
    getNav,
    getSharedTotalStake,
    addMinter,
    getETHTVL,
    getLPTVL,
    getSharedStakingAPR,
    getLPReward,
    getBetaStakingBalance,
    withdrawBetaShare,
    getUserSharedStaking,
    currentDay,
    doSettle,
    split,
    merge,
    getLendingApr,
  });
}
export default createHookContext(useWalletModel);
